a, a:hover {
  color: #000;
  text-decoration: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cus_bnr {
  background-image: url(./bgjum.jpg);
  background-size: cover;
  background-position: center;
  color: #fff;
  text-shadow: 0 0 5px rgba(0,0,0,0.6);
  word-break: keep-all;
}

@media screen and (max-width: 768px) {
  .md_br {
    display: none;
  }
}

.watched_box {
  position: fixed;
  top: 120px;
  right: 5%;
  padding: 8px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
}
.watched_link:nth-child(n+1) {
  display: block;
  margin-top: 8px;
}
.watched_img {
  width: 120px;
  border: 1px solid #eee;
}
.watched_img:hover {
  opacity: .8;
}
.watched_nothing {
  width: 120px;
  line-height: 1.8;
  font-size: 14px;
  color: #666;
  padding: 30px 0;
  word-break: keep-all;
}